import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import 'bulma/css/bulma.css';

const colorscheme = () => {
  document.querySelector('body').classList.toggle("theme_dark");
  document.querySelector('#colbtn').classList.toggle('is-warning');
  document.querySelector('#navicon').classList.toggle('ti-light-bulb');
  // let dark = document.querySelector('#colbtn').innerHTML='dark';
  // alert(light);
}

const menutoggle = () => {
  document.querySelector('#navbarBasicExample').classList.toggle('is-active');

}
const Header = ({ siteTitle }) => (
  <header>
    <div >
      <h1 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            color: `white`,
            textDecoration: `none`,
          }}
        >
          
        </Link>
      </h1>
    </div>

    <nav className="navbar is-dark" role="navigation" aria-label="main navigation">
      
    <div className="navbar-brand">
    <Link className="navbar-item" to="/">
    {siteTitle}
    </Link>

    <span role="button" onClick={menutoggle} className="navbar-burger" aria-label="menu" aria-expanded="false">
      <span aria-hidden="true"></span>
      <span aria-hidden="true"></span>
      <span aria-hidden="true"></span>
    </span>
  </div>

  <div id="navbarBasicExample" className="navbar-menu">
    {/* <div className="navbar-start">
      
      </div> */}

    <div className="navbar-end">

    <Link className="navbar-item" to="/">
        Home
      </Link>
      <Link className="navbar-item" to="/about">
        About
      </Link>

      <Link className="navbar-item" to="/blog">
        Blog
      </Link>

      

     
     
      <div className="navbar-item">
        <div className="buttons">
        <button className="button is-black" id="colbtn" onClick={colorscheme}><span id="navicon" className="ti-star"></span></button>

         
        </div>
      </div>

</div>
      </div>
    </nav>

  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
